.atribution{
  margin: 1em 0;
  text-align: center;
}
.atribution a {
  color: var(--text);
}
.header{
  top: 0;
  position: sticky;
  min-height: 15vh;
  box-shadow: 0 0 5px 0 rgba(0,0,0,.5);
  background-color: var(--backgrundColorItems);

  display: flex;
  align-items: center;
  justify-content: space-around; 
}
.title{
  font-weight: 600;
  font-size: 3em;
  color: var(--items);
  text-decoration: none;
  text-shadow: 2px 1px 2px rgba(0,0,0,.5);
}
.fa-bars{
  cursor: pointer;
  font-size: 1.4em;
  color: var(--text);
}